const DEFAULT_ROW_WIDTH = '100%'
const DEFAULT_LAST_ROW_WIDTH = '60%'

export default {
  props: {
    loading: {
      type: Boolean,
      default: true
    },
    imgTitle: {
      type: Boolean,
      default: false
    },
    showAvatar: {
      type: Boolean,
      default: true
    },
    avatarSize: {
      type: String,
      default: '100rpx'
    },
    avatarShape: {
      type: String,
      default: 'round' // square | round
    },
    showTitle: {
      type: Boolean,
      default: true
    },
    // titleWidth: {
    //   type: String,
    //   default: '80%',
    // },
    row: {
      type: Number,
      default: 3
    },
    category: {
      type: Number,
      default: 0
    },
    animate: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {}
  },
  computed: {
    rowList() {
      const list = []
      for (let i = 0; i < this.row; i++) {
        list.push({
          width: i === this.row - 1 && i !== 0 ? DEFAULT_LAST_ROW_WIDTH : DEFAULT_ROW_WIDTH
        })
      }
      return list
    }
  }
}
